body {
  background: #f4f4f4;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
}

.card {
  background: #fff;
  padding: 25px;
  margin-bottom: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.logo {
  height: 60px;
}

label {
  display: block;
  margin-bottom: 12px;
}

input,
select {
  width: 100%;
  padding: 6px;
  margin-top: 4px;
}

table {
  width: 100%;
  margin-top: 10px;
  border-collapse: collapse;
}

td {
  padding: 6px 0;
}

.total td {
  border-top: 2px solid #000;
  font-weight: bold;
}

ul {
  margin-left: 20px;
}

.note {
  font-size: 12px;
  margin-top: 10px;
}

.footer {
  margin-top: 20px;
  font-size: 12px;
}

button {
  padding: 10px 20px;
  margin-right: 10px;
  font-size: 15px;
  cursor: pointer;
}

/* ================== OTSIKOT ================== */

h1 {
  font-size: 26px;
  margin-bottom: 20px;
}

h2 {
  font-size: 17px;
  font-weight: bold;
  letter-spacing: 1px;
  border-bottom: none;          /* poistaa border-viivan */
  text-decoration: none;        /* poistaa alleviivauksen */
  padding-bottom: 0;
  margin-bottom: 20px;
}

h3 {
  font-size: 18px;
  margin-top: 25px;
  margin-bottom: 10px;
}

/* ================== TARJOUS / SOPIMUS ================== */

.card {
  
}

p {
  margin: 6px 0;
  line-height: 1.4;
}

/* ================== TAULUKOT ================== */

table td:last-child {
  text-align: right;
}

.total td {
  padding-top: 10px;
  font-size: 16px;
}

/* ================== LISTAT ================== */

ul li {
  margin-bottom: 4px;
}

/* ================== HEADER (LOGO + OTSIKKO) ================== */

.header {
  border-bottom: none;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.logo {
  height: 55px;
}

/* ================== FOOTER / YHTEYSTIEDOT ================== */

.footer {
  border-top: 1px solid #ddd;
  padding-top: 10px;
  margin-top: 30px;
  color: #333;
}

/* ================== ALLEKIRJOITUKSET ================== */

.signature {
  margin-top: 40px;
}

.signature p {
  margin-top: 30px;
}

/* ================== BUTTONIT ================== */

button {
  background: #1f2937;
  color: #fff;
  border: none;
  border-radius: 4px;
}

button:hover {
  background: #111827;
}

/* ================== TULOSTUS / PDF ================== */

@media print {
  body {
    background: #fff;
  }

  .card {
    box-shadow: none;
    border: none;
  }

  button {
    display: none;
  }
}

